import VeDropdown from "vue-easytable/packages/ve-dropdown";
import { COMPS_NAME, EMIT_EVENTS } from "./util/constant";
import { clsName } from "./util/index";
import VeIcon from "vue-easytable/packages/ve-icon";
import { ICON_NAMES } from "../../src/utils/constant";

export default {
    name: COMPS_NAME.VE_SELECT,
    props: {
        width: {
            type: Number,
            default: 90,
        },

        // select()
        maxWidth: {
            type: Number,
            default: 0,
        },

        // true  checkbox
        isMultiple: {
            type: Boolean,
            default: false,
        },

        // v-model  [{value/label/selected}]
        value: {
            type: Array,
            default: null,
        },

        // 
        placeholder: {
            type: String,
            default: "",
            validator: function (value) {
                return value.length > 0;
            },
        },

        //  left|center|right
        textAlign: {
            type: String,
            default: "left",
        },

        // input
        isInput: {
            type: Boolean,
            default: false,
        },
        // popper append to element
        popperAppendTo: {
            type: [String, HTMLElement],
            default: function () {
                return document.body;
            },
        },
    },
    data() {
        return {
            visible: false,

            internalOptions: [],

            inputValue: "",
            // dorpdown visible
            dropdownVisible: false,
        };
    },
    computed: {
        // icon class
        iconClass() {
            return {
                [clsName("show")]: this.dropdownVisible,
                [clsName("toggle-icon")]: true,
            };
        },
    },
    watch: {
        value: function () {
            this.init();
        },
    },
    methods: {
        // 
        init() {
            this.internalOptions = Object.assign([], this.value);
        },

        // 
        showSelectInfo() {
            var result, labels;

            labels = this.selectedLabels();
            if (Array.isArray(labels) && labels.length > 0) {
                result = labels.join();
            } else {
                result = this.placeholder;
            }

            return result;
        },

        // label
        selectedLabels() {
            return this.internalOptions
                .filter((x) => x.selected)
                .map((x) => {
                    if (x.selected) {
                        return x.label;
                    }
                });
        },

        // dropdown change event
        dropdownChange() {
            // v-model 
            this.$emit("input", this.internalOptions);
            this.$emit(EMIT_EVENTS.SELECT_CHANGE, this.internalOptions);
        },
    },

    created() {
        this.init();
    },
    render() {
        const { isInput } = this;

        const props = {
            class: "ve-select",
            props: {
                isSelect: true,
                width: this.width,
                maxWidth: this.maxWidth,
                isMultiple: this.isMultiple,
                textAlign: this.textAlign,
                isInput: this.isInput,
                // v-model
                value: this.internalOptions,
                hideByItemClick: true,
                popperAppendTo: this.popperAppendTo,
            },
            style: {
                width: this.width,
            },
            on: {
                //change: this.dropdownChange,
                // v-model
                input: (val) => {
                    this.internalOptions = val;
                    this.dropdownChange();
                },
                // dropdown visible change
                "dropdown-visible-change": (visible) => {
                    this.dropdownVisible = visible;
                },
            },
        };

        let content = "";
        if (isInput) {
            content = (
                <input
                    class={clsName("input")}
                    placeholder={this.placeholder}
                    type="text"
                    v-model={this.inputValue}
                />
            );
        } else {
            content = (
                <span class={clsName("selected-span")}>
                    {this.showSelectInfo()}
                </span>
            );
        }

        return (
            <VeDropdown {...props}>
                <span>
                    {content}
                    <VeIcon
                        name={ICON_NAMES.BOTTOM_ARROW}
                        class={this.iconClass}
                    />
                    {/* <i class={[this.iconClass]}></i> */}
                </span>
            </VeDropdown>
        );
    },
};
